
import React from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import './login.css';
import { Form, Icon, Input, Button, Checkbox, Row, Col } from 'antd';
import App from './signup';
import { Typography } from 'antd';

const { Title, Paragraph } = Typography;

class NormalLoginForm extends React.Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div>
        <Row>
          <Col>

            <Typography>
              <div >
                <Title level={2} >Login to use all features.</Title>
              </div>
              <br />
              <Paragraph style={{ fontSize: "large" }}>
                Welcome to Besafe!
            Login/Signup to take a step closer towards safety and prevention against crime, and to use our important services.
            You can simply register yourself with your email-id and you have to simply fill in the details of at most three contacts of your close ones,
             with whom you wish to share your location or relevant details at the time of emergency.
      </Paragraph>
            </Typography>
          </Col>
        </Row>
        <br />
        <br />
        <Row>
          <div align="center">
            <Col>
              <Form onSubmit={this.handleSubmit} className="login-form">
                <Form.Item>
                  {getFieldDecorator('username', {
                    rules: [{ required: true, message: 'Please input your username!' }],
                  })(
                    <Input
                      prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} id="user" />}
                      placeholder="Username"
                    />,
                  )}
                </Form.Item>
                <Form.Item>
                  {getFieldDecorator('password', {
                    rules: [{ required: true, message: 'Please input your Password!' }],
                  })(
                    <Input
                      prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                      type="password"
                      placeholder="Password"
                    />,
                  )}
                </Form.Item>
                <Form.Item>
                  {getFieldDecorator('remember', {
                    valuePropName: 'checked',
                    initialValue: true,
                  })(<Checkbox>Remember me</Checkbox>)}
                  <a className="login-form-forgot" href="">
                    Forgot password
          </a>
                  <Button type="primary" htmlType="submit" className="login-form-button">
                    Log in
          </Button>
                  Or
          <Row>
                    <Col xs={{ span: 2, offset: 1 }} lg={{ span: 4, offset: 2 }}>
                      <App />
                    </Col>
                  </Row>

                </Form.Item>
              </Form>
            </Col>
          </div>
        </Row>
      </div>
    );
  }
}

const WrappedNormalLoginForm = Form.create({ name: 'normal_login' })(NormalLoginForm);

export default WrappedNormalLoginForm;

ReactDOM.render(<WrappedNormalLoginForm />, document.getElementById('container'));
