
import React from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import { Menu, Avatar, notification, Icon } from 'antd';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import dp from './accounting.png';

class App extends React.Component {
  state = {
    current: 'mail',
  };

  handleClick = e => {
    console.log('click ', e);
    this.setState({
      current: e.key,
    });
  };

  openNotification = () => {
    notification.open({
      message: 'You are not signed in.',
      description:
        'Login to use all the features.',
      icon: <Icon type="smile" style={{ color: '#108ee9' }} />,
    });
  };


  render() {
    return (
      <Router>
      <Menu theme="dark" onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
        <Menu.Item key="mail" style={{float:"right"}}>
       <Avatar color="black"  onClick={this.openNotification}><img src={dp} style={{width:"2rem"}}/></Avatar>
    
        </Menu.Item>
      </Menu>
      </Router>
    );
  }
}

const Head= (App);
export default Head;

ReactDOM.render(<App />, document.getElementById('container'));
          